<template>
  <div class="main">
      <header class="mainHeader">
        <p>date of rates <strong>{{ siglas.date }}</strong></p>
      </header>
        <div class="mainLeftPannel">
          <div class="space">
            <input type="text" class="input is-half is-medium" id="inputSize" v-model="moneyIn" @keyup="calculateMoneyOut" @keypress="onlyNumber">
            <select class="select is-medium" name="moneyIn" id="moneyIn" v-model="moneyInOption"
              @change="calculateMoneyIn">
              <option v-for="(value, name, index) in siglas.rates" :key="index" :value="value" :moneyInOption="value"
                v-bind:moneyInSigla="name">
                {{name}}
              </option>
            </select>
          </div>
          <div class="space">
            <input type="text" class="input is-half is-is-inline is-medium" id="inputSize" v-model="moneyOut"
              @keyup="calculateMoneyIn" @keypress="onlyNumber">
            <select class="select is-inline is-medium" name="moneyOut" id="moneyOut" v-model="moneyOutOption"
              @change="calculateMoneyOut">
              <option v-for="(value, name, index) in siglas.rates" :value="value" :key="index">
                {{name}}
              </option>
            </select>
          </div>
        </div>

          <img src="../assets/exchange.jpg" class="mainRightPannel absolute-ico" alt="">

      <footer class="mainFooter">
        <p class="mainFooterP">
          current and historical foreign exchange rates published by the <a href="">European Central Bank</a>
        </p>
      </footer>
    </div>
</template>

<script>
import axios from 'axios';
export default {
        data() {
      return {
        moneyIn: '1',
        moneyOut: '',
        moneyInOption: '',
        moneyOutOption: '',
        moneyInSigla: '',
        moneyOutSigla: '',
        EUR: "",
        siglas: {},
      }
    },
    created: function () {
      axios.get("https://api.exchangeratesapi.io/latest").then(response => {
        this.EUR = response.data
        this.siglas = response.data
        this.moneyInOption = response.data.rates.BRL
        this.moneyOutOption = response.data.rates.USD
        this.siglas.rates['EUR'] = 1
        console.log(response)
        this.calculateMoneyOut()
      })
    },

    methods: {
      calculateMoneyOut: function () {
        this.moneyOut = (Number(this.moneyIn) / this.moneyInOption) / (1 / this.moneyOutOption)
        this.moneyOut = this.moneyOut.toFixed(2)
        console.log(this.moneyOut)
      },
      calculateMoneyIn: function () {
        this.moneyIn = (Number(this.moneyOut) / this.moneyOutOption) / (1 / this.moneyInOption)
        this.moneyIn = this.moneyIn.toFixed(2)
      },
      onlyNumber ($event) {
        //console.log($event.keyCode); //keyCodes value
        let keyCode = ($event.keyCode ? $event.keyCode : $event.which);
        if ((keyCode < 48 || keyCode > 57) && keyCode !== 46) { // 46 is dot
          $event.preventDefault();
   }
}
    }
}
</script>

<style scoped>

.mainHeader{
  grid-area: mainHeader;
  margin: 2% 0 0 2%
}
.mainLeftPannel{
  width: 100%;
  grid-area: mainLeftPannel;
  justify-self: start;
}
.mainRightPannel{
  width: 80%;
  grid-area: mainRightPannel;
  align-self: center;
  justify-self: center;
}
.mainFooter{
  grid-area: mainFooter;
  margin: 0 0 2% 2%;
}
#inputSize{
  display: inline-block;
  width: 65%;
  margin-right: 6%;
}
.space{
  margin: 10% 0 0 10%;
}
p{
  font-size: 15px;
}
a{
  font-weight: bolder;
}
.absolute-ico{

  width: 70%;
}
</style>